import React from 'react';

const AboutUs = ()=> {
    return(
        <div style={{textAlign: 'left', height:'69vh', padding: '5% 10% 0 10%'}}>
        <h1>About Us</h1>
        <br/>
        <p>Hello friends,</p>
        <p>I Manoj Krishna wanted to share my personal body building experiences with this website. 
         I'm neither a nutritionist nor a fitness trainer. I have listed what had best worked out for me. 
         Please take this workouts and diet as reference only.
        </p>
        <p>Me along with Sriram Rudraraju developed this website using the images found in google search results. 
            We havent found any copyrights on these used images and other content in this website. 
            If we unknowingly violated anything, please let us know. We will change them as soon as possible.</p>
        </div>
    )
}

export default AboutUs;


